import React from "react";

type State = {
  typeContract: string | undefined,
  isLoading: boolean
}

enum ActionTypes {
  set_type_contract = 'set_type_contract',
}

type Action = {
  type: ActionTypes;
  payload?: any;
};

const reducer = (prevState: State, action: Action): State => {
  switch (action.type) {
    case ActionTypes.set_type_contract:
      return {
        ...prevState,
        typeContract: action.payload,
        isLoading: true
      };
    default:
      return prevState;
  }
};

const CreateContractContext = React.createContext({
  changeTypeContract: (data: string | undefined) => { },
});

const useCreateContract = () => React.useContext(CreateContractContext);

const CreateContractProvider = ({
  children,
}: {
  children: (data: State) => React.ReactNode;
}) => {

  const [state, dispatch] = React.useReducer(reducer, {
    typeContract: undefined,
    isLoading: false
  });



  const authContext = React.useMemo(
    () => ({
      changeTypeContract: (data: string | undefined) => {
        dispatch({ type: ActionTypes.set_type_contract, payload: data });
      },
    }),
    [],
  );

  return (
    <CreateContractContext.Provider value={authContext}>
      {children({ ...state })}
    </CreateContractContext.Provider>
  );
};

export { useCreateContract };
export default CreateContractProvider;